import { useState } from "react";
import { styled } from "@mui/material/styles";
import { Box, Button, Container, Drawer, FilledInput, FormControl, IconButton, OutlinedInput, Typography, useMediaQuery } from "@mui/material";
import MenuIcon from '@mui/icons-material/Menu';
import SearchIcon from '@mui/icons-material/Search';
import { logoImg, headerImg } from "../../assets/image";
import { PrimaryButton, SecondaryButton } from "../button/button";
import { Menu, MenuItem } from "../menu/menu";

const headerItems: HeaderItem[] = [
	{ label: "Trang chủ", href: "/" },
	{ label: "Thể thao", href: "/the-thao-vip52" },
	{ label: "Game bài", href: "/game-bai-vip52" },
	{ label: "Tài xỉu", href: "/tai-xiu-vip52" },
	{ label: "bắn cá", href: "/ban-ca-vip52" },
	{ label: "nổ hũ", href: "/no-hu-vip52" },
	{ label: "Live casino", href: "/live-casino-vip52" },
	{ label: "Lô đề", href: "/lo-de-vip52" },
	{ label: "Hướng dẫn", href: "/huong-dan" },
]

const Header = () => {
	const isMobile = useMediaQuery('(max-width:768px)');

	const [open, setOpen] = useState(false);
	const [search, setSearch] = useState("");

	const toggleDrawer = (newOpen: boolean) => () => {
		setOpen(newOpen);
	};

	return (
		<HeaderWrapper isMobile={isMobile}>
			<style>{stuckMoveDown}</style>
			<Container maxWidth="xl2">
				<HeaderContainer isMobile={isMobile}>
					{isMobile &&
						<IconButton size="large" edge="start"
							onClick={toggleDrawer(true)}
							sx={{ position: "absolute", left: "10px" }}
							aria-label="menu"
						>
							<MenuIcon />
						</IconButton>
					}
					<LogoWrapper href="#roadmap">
						<img src={logoImg} alt="logo" />
					</LogoWrapper>

					<DesktopMenuWrapper>
						{headerItems.map((item, index) => (
							<a href={item.href} key={index}>
								<MenuItem>
									<Typography key={index} variant="body1">
										{item.label}
									</Typography>
								</MenuItem>
							</a>
						))}
					</DesktopMenuWrapper>
					{!isMobile && (
						<Box justifyContent={"center"} flexDirection={"row"} alignItems={"center"}>
							<PrimaryButton sx={{ p: "4px", px: "4px" }} >
								<Typography variant="body1" fontSize={"16px"}>ĐĂNG NHẬP</Typography>
							</PrimaryButton>
							<SecondaryButton sx={{ p: "4px", px: "4px", ml: "5px" }}>
								<Typography variant="body1" fontSize={"16px"}>ĐĂNG KÝ</Typography>
							</SecondaryButton>
						</Box>
					)}
				</HeaderContainer>
			</Container>
			{isMobile && (
				<Box justifyContent={"center"} display={"flex"} flexDirection={"row"} sx={{ bgcolor: "#57088c" }} width={"100%"} py={1} alignItems={"center"}>
					<PrimaryButton sx={{ p: "4px", px: "4px" }} >
						<Typography variant="body1" fontSize={"16px"}>ĐĂNG NHẬP</Typography>
					</PrimaryButton>
					<SecondaryButton sx={{ p: "4px", px: "4px", ml: "5px" }}>
						<Typography variant="body1" fontSize={"16px"}>ĐĂNG KÝ</Typography>
					</SecondaryButton>
				</Box>
			)}
			<Drawer open={open} onClose={toggleDrawer(false)} PaperProps={{ style: { width: 240, backgroundColor: "hsl(0deg 0% 100% / 95%)" } }} >
				<MobileMenuWrapper  >
					<FormControl sx={{ width: '100%', mt: 5, px: 2 }} size={"small"} variant="filled">
						<FilledInput
							id="filled-adornment-password"
							type={'text'}
							placeholder="Search..."
							sx={{ color: "black", borderBottom: "1px solid #7f7d7d", }}
							endAdornment={<SearchIcon />}
						/>
					</FormControl>
					<Box sx={{ width: "100%", flexDirection: "column", display: "flex", py: 3 }}>
						{headerItems.map((item, index) => (
							<MMenuItem href={item.href} key={index} sx={{ px: 3 }}>
								<Typography key={index} variant="h5" color={'hsl(0deg 0% 40% / 85%)'}>
									{item.label}
								</Typography>
							</MMenuItem>
						))}
					</Box>
					<Box display={"flex"} justifyContent={"center"} flexDirection={"row"} alignItems={"center"}>
						<PrimaryButton sx={{ p: "8px", px: "6px" }} >
							<Typography variant="body1" fontSize={"16px"}>ĐĂNG NHẬP</Typography>
						</PrimaryButton>
						<SecondaryButton sx={{ p: "8px", px: "6px", ml: "5px" }}>
							<Typography variant="body1" fontSize={"16px"}>ĐĂNG KÝ</Typography>
						</SecondaryButton>
					</Box>
				</MobileMenuWrapper>
			</Drawer>
		</HeaderWrapper>
	)
}

const stuckMoveDown = `  
  @keyframes stuckMoveDown {  
    from {  
      transform: translateY(-100px);  
    }  
    to {  
      transform: translateY(0); /* Adjust the value as needed */  
    }  
  }  
`;

const HeaderWrapper = styled(Box)<{ isMobile: boolean }>(({ theme, isMobile }) => ({
	position: "sticky",
	top: 0,
	zIndex: theme.zIndex.appBar,
	display: 'flex',
	alignItems: 'center',
	backdropFilter: "blur(10px)",
	boxShadow: `1px 1px 10px ${theme.palette.common.headerShadow}`,
	animation: 'stuckMoveDown 0.6s ease 0s 1 normal forwards',
	backgroundImage: `url(${headerImg})`,
	backgroundSize: 'cover',
	flexDirection: isMobile ? "column" : "row",
	backgroundPosition: 'center',
	'&::before': {
		content: '""',
		position: 'absolute',
		top: 0,
		left: 0,
		right: 0,
		bottom: 0,
		backgroundColor: 'rgba(123,0,224,0.8)',
		zIndex: -1,
	},
}));

const HeaderContainer = styled(Box)<any>(({ theme, isMobile }: any) => ({
	flex: 1,
	display: 'flex',
	alignItems: 'center',
	justifyContent: isMobile ? "center" : 'space-between',
	height: "70px",
	backgroundFilter: "blur(10px)",

	[theme.breakpoints.down('md')]: {
		height: "60px",
	}
}))

const LogoWrapper = styled("a")(({ theme }) => ({
	display: 'flex',
	alignItems: 'center',
	height: "100%",
	padding: "15px 0px",
	cursor: 'pointer',
	img: {
		width: "180px",
	},
	[theme.breakpoints.down('md')]: {
		padding: '10px 0px',
	}
}))

const DesktopMenuWrapper = styled(Box)(({ theme }) => ({
	gap: '5px',
	display: 'flex',
	alignItems: 'center',
	height: '100%',

	a: {
		height: '100%',
		textDecoration: 'none',
	},

	[theme.breakpoints.down('md')]: {
		display: 'none',
	},
}))

const MMenuItem = styled('a')(() => ({
	textDecoration: 'none',
	color: 'inherit',
	paddingTop: "10px",
	paddingBottom: "10px",
	transition: "all 0.3s",
	'&:hover': {
		backgroundColor: "#0000003d"
	}
}))

const MobileMenuWrapper = styled(Box)(({ theme }) => ({
	display: 'flex',
	flexDirection: "column",
}))

export { Header }