import { Box } from "@mui/material";
import { styled } from "@mui/material/styles";

import { headerImg } from "../assets/image";
import { Banner } from "./components/banner";
import { Contacts } from "./components/contacts";
import { Stores } from "./components/store";
import { Promition } from "./components/promotional";
import { TopGame } from "./components/topgame";
import { PopularGames } from "./components/populargames";
import { BettingGuide } from "./components/bettingguides";
import { Introduction } from "./components/introduction";
const HomePage = () => {
	return (
		<Container>
			<Background >
				<Box component={"img"}
					width={1836}
					height={588}
					src={headerImg}
					sx={{
						objectFit: "cover",
						height: "100%",
						position: "absolute",
						objectPosition: "50% 50%",
						top: 0,
						left: 0,
						width: "100%",
					}} />
				<Box sx={{
					backgroundColor: 'rgb(232 95 63 / 19%)',
					position: "absolute",
					height: "100%",
					width: "100%",
					left: 0,
					top: 0,
					margin: "0",
					padding: "0"
				}} />
			</Background>
			<Banner />
			<Container >
				<Stores />
				<Promition />
				<TopGame />
				<PopularGames />
				<Introduction />
				<Contacts />
				<BettingGuide />
			</Container>
			{/* <Hero />
			<SportsWinPrizes />
			<Poster />
			<Blogs />
			<TokenStaking />
			<Features /> */}
		</Container>
	)
}

const Container = styled(Box)(({ theme }) => ({
	display: "flex",
	flexDirection: "column",
	backgroundColor: "rgba(5, 5, 5, 0.762)",
	position: "relative",
}))

const Background = styled(Box)(({ theme }) => ({
	position: "absolute",
	height: "100%",
	width: "100%",
	top: 0,
	let: 0,
	margin: 0,
	padding: 0
}))



export { HomePage };